import React from 'react'
import ScrollableAnchor from 'react-scrollable-anchor'

import Project from './Project/Project'
import natours from '../../../assets/img/natours.jpg'
import omnifood from '../../../assets/img/omnifood.jpeg'
import trillo from '../../../assets/img/trillo.jpeg'
import forkify from '../../../assets/img/forkify.jpg'
import faceRecognition from '../../../assets/img/face_recognition.jpg'
import burgerBuilder from '../../../assets/img/burger_builder.jpg'

const projectDetails = {
  project1: {
    name: 'Natours',
    image: natours,
    projectHref: 'https://raybdev.github.io/natours/',
    imageAlt: 'Natours Project Photo',
    githubHref: 'https://github.com/RayBDev/natours',
  },
  project2: {
    name: 'Omnifood',
    image: omnifood,
    projectHref: 'https://raybdev.github.io/omnifood/build',
    imageAlt: 'Omnifood Project Photo',
    githubHref: 'https://github.com/RayBDev/omnifood',
  },
  project3: {
    name: 'Trillo',
    image: trillo,
    projectHref: 'https://raybdev.github.io/trillo/',
    imageAlt: 'Trillo Project Photo',
    githubHref: 'https://github.com/RayBDev/trillo',
  },
  project4: {
    name: 'Forkify',
    image: forkify,
    projectHref: 'https://raybdev.github.io/forkify/dist/index.html',
    imageAlt: 'Forkify Project Photo',
    githubHref: 'https://github.com/RayBDev/forkify',
  },
  project5: {
    name: 'Face Recognition Brain',
    image: faceRecognition,
    projectHref: 'https://facerecognition-smartbrain.herokuapp.com/',
    imageAlt: 'Face Recognition Project Photo',
    githubHref: 'https://github.com/RayBDev/FaceRecognitionApp',
  },
  project6: {
    name: 'Burger Builder',
    image: burgerBuilder,
    projectHref: 'https://burger-builder-93afc.firebaseapp.com/',
    imageAlt: 'Burger Builder Project Photo',
    githubHref: 'https://github.com/RayBDev/burger-builder',
  },
}

let projects = []
for (let key in projectDetails) {
  projects.push(
    <Project
      key={key}
      name={projectDetails[key].name}
      image={projectDetails[key].image}
      projectHref={projectDetails[key].projectHref}
      imageAlt={projectDetails[key].imageAlt}
      githubHref={projectDetails[key].githubHref}
    />
  )
}

const Portfolio = () => (
  <ScrollableAnchor id={'section-portfolio'}>
    <section className="section-portfolio">
      <h2 className="heading__secondary">
        My <span>Portfolio</span>
      </h2>
      <div className="portfolio">{projects}</div>
    </section>
  </ScrollableAnchor>
)

export default Portfolio



