import React, { Component } from 'react'

import NavLink from './NavLink/NavLink'

class Navigation extends Component {
  state = {
    links: [
      {
        name: 'About Me',
        address: '#section-about',
        target: '_self',
      },
      {
        name: 'My Portfolio',
        address: '#section-portfolio',
        target: '_self',
      },
      {
        name: 'Contact Me',
        address: '#section-contact',
        target: '_self',
      },
      {
        name: 'Resume PDF',
        address: '/static/Ray_Bernard_Resume.pdf',
        target: '_blank',
      },
    ],
    checked: false,
  }

  linkClickHandler = () => {
    let linkState = [...this.state.links]
    this.setState({
      links: linkState,
      checked: !this.state.checked,
    })
  }

  render() {
    let menuLinks = this.state.links.map((link, index) => (
      <NavLink
        key={index}
        linkName={link.name}
        linkAddress={link.address}
        linkTarget={link.target}
        linkNum={`0${index + 1}`}
        clicked={this.linkClickHandler}
      />
    ))
    return (
      <div className="navigation">
        <input
          type="checkbox"
          className="navigation__checkbox"
          id="navi-toggle"
          checked={this.state.checked}
        />
        <label onClick={this.linkClickHandler} className="navigation__button">
          <span className="navigation__icon">&nbsp;</span>
        </label>
        <div className="navigation__background">&nbsp;</div>

        <nav className="navigation__nav">
          <ul className="navigation__list">{menuLinks}</ul>
        </nav>
      </div>
    )
  }
}

export default Navigation



