import React from 'react'

import logo from '../../../assets/img/logo-yellow-white.png'
import Icon from '../../UI/Icon/Icon'

const currentYear = new Date().getFullYear()

const Footer = () => (
  <section className="section-footer">
    <div className="footer">
      <div className="footer__details">
        <div className="footer__details-contact">
          <div className="paragraph">
            <div className="contact__info-details">
              <Icon clName="contact__info-icon" icName="phone" />
              +1 (403) 744-5150
            </div>
            <div className="contact__info-details">
              <Icon clName="contact__info-icon" icName="mail" />
              <a href="mailto:info@rbernard.ca" className="paragraph__link">
                info@rbernard.ca
              </a>
            </div>
          </div>
        </div>
        <div className="footer__details-social">
          <a
            href="https://ca.linkedin.com/in/ray-bernard-3274a335"
            target="_blank"
          >
            <Icon clName="footer__details-social-linkedin" icName="linkedin" />
          </a>
          <a href="https://github.com/RayBDev" target="_blank">
            <Icon clName="footer__details-social-github" icName="github" />
          </a>
        </div>
      </div>
      <div className="footer__logo-box">
        <img src={logo} alt="RBERNARD Logo" className="footer__logo" />
      </div>
      <div className="footer__copy">
        <p className="paragraph">&copy; 2017-{currentYear} RBERNARD.CA</p>
      </div>
    </div>
  </section>
)

export default Footer



