import React from 'react'

import Icon from '../../../UI/Icon/Icon'

const Project = props => (
  <div className="portfolio__project">
    <a
      href={props.projectHref}
      className="portfolio__project-imagebox-link"
      target="_blank"
    >
      <figure className="portfolio__project-imagebox">
        <img
          src={props.image}
          alt={props.imageAlt}
          className="portfolio__project-image"
        />
        <figcaption className="portfolio__project-caption">
          <Icon clName="portfolio__project-caption-icon" icName="eye" />
        </figcaption>
      </figure>
    </a>
    <a
      href={props.projectHref}
      target="_blank"
      className="portfolio__project-view"
    >
      <Icon clName="portfolio__project-view-icon" icName="eye" />Project
    </a>
    <a
      href={props.githubHref}
      target="_blank"
      className="portfolio__project-source"
    >
      <Icon clName="portfolio__project-source-icon" icName="github" />Github
    </a>
  </div>
)

export default Project



