import React from 'react'

import Icon from '../../../UI/Icon/Icon'

const Skill = props => (
  <div className="about__skills-box">
    <div className="about__skills-iconbox">
      <Icon clName="about__skills-icon" icName={props.icon} />
    </div>
    <h4 className="heading__skills">{props.heading}</h4>
    <div className="about__skills-description">{props.text()}</div>
  </div>
)

export default Skill



