import React from 'react'
import ScrollableAnchor from 'react-scrollable-anchor'

import Aux from '../../hoc/Aux/Aux.js'
import Skill from './Skill/Skill'

import photo from '../../../assets/img/photo.jpg'

const skillInfo = {
  html5: {
    heading: 'HTML5 & CSS3',
    icon: 'html5',
    text: () => (
      <Aux>
        <p className="paragraph">
          It is important to understand how HTML affects SEO. Therefore, Ray
          puts a strong emphasis on meta tags, semantic elements, and heading
          placement.
        </p>
        <p className="paragraph">
          He also has an in depth command of CSS3 properties and how they affect
          design. He incorporates design best practices in color coordination,
          text selection, animations, flex/grid layouts, and responsive images.
        </p>
      </Aux>
    ),
  },
  sass: {
    heading: 'Sass',
    icon: 'sass',
    text: () => (
      <Aux>
        <p className="paragraph">
          Managing CSS3 code can become a complicated affair as project design
          continues to expand.
        </p>
        <p className="paragraph">
          Ray uses Sass to de-clutter and stay organized with his CSS3 designs.
          This also allows his code to be modular and reusable.
        </p>
      </Aux>
    ),
  },
  javascript: {
    heading: 'JavaScript',
    icon: 'javascript',
    text: () => (
      <Aux>
        <p className="paragraph">
          JavaScript is the backbone to just about every project Ray works on.
          He is proficient at Vanilla JavaScript and interacting with the DOM.
        </p>
        <p className="paragraph">
          This allows Ray to make websites interactive and provide powerful user
          experiences.
        </p>
      </Aux>
    ),
  },
  react: {
    heading: 'React',
    icon: 'react',
    text: () => (
      <Aux>
        <p className="paragraph">
          Ray uses the React Framework on a daily basis. Using React along with
          his JavaScript expertise allows him to create user-friendly web
          applications in the shortest amount of time.
        </p>
        <p className="paragraph">
          Ray uses Redux for state management, Router for page routing, NextJS
          for SSR and Gatsby for static pages.
        </p>
      </Aux>
    ),
  },
  node: {
    heading: 'Node',
    icon: 'node-dot-js',
    text: () => (
      <Aux>
        <p className="paragraph">
          To continue his love for Javascript, Ray uses Node.JS for his backend
          architecture.
        </p>
        <p className="paragraph">
          This includes creating RESTful APIs, using ExpressJS for building web
          servers, MONGODB as a database, and using Socket.io for two way
          communication in chat applications.
        </p>
      </Aux>
    ),
  },
  git: {
    heading: 'Git',
    icon: 'github',
    text: () => (
      <Aux>
        <p className="paragraph">
          Working on a team calls for a structured form of version control.
          Ray's favorite version control system is Git and he uses it on almost
          every project.
        </p>
        <p className="paragraph">
          He prefers to use GitHub as his repository hosting service due to its
          strict security policies and ease of use.
        </p>
      </Aux>
    ),
  },
}

let skills = []
for (let key in skillInfo) {
  skills.push(
    <Skill
      key={key}
      heading={skillInfo[key].heading}
      icon={skillInfo[key].icon}
      text={skillInfo[key].text}
    />
  )
}

const About = () => (
  <ScrollableAnchor id={'section-about'}>
    <section className="section-about">
      <div className="about">
        <h2 className="heading__secondary">
          About <span>Me</span>
        </h2>
        <div className="about__bio">
          <figure className="about__bio-photobox">
            <img
              src={photo}
              alt="Ray Bernard Face Photo"
              className="about__bio-photo"
            />
            <figcaption className="about__bio-caption">Ray Bernard</figcaption>
          </figure>
          <div className="about__bio-textbox">
            <h3 className="heading__tertiary">BIO</h3>
            <p className="paragraph">
              Ray has been building websites since 2002. Back then, he used
              Microsoft FrontPage and Adobe Dreamweaver to start learning code.
              His initial work was focused on small online businesses and
              converting users into buyers with sales copy and call to action
              triggers.
            </p>
            <p className="paragraph">
              Today, Ray has developed a true passion for web development and
              design. He uses multiple technologies to create beautiful user
              interfaces and highly functional user experiences. Ray works with
              clients from all over the world and builds everything from static
              websites to complex web applications.
            </p>
          </div>
        </div>
        <h3 className="heading__tertiary">My Technical Skills:</h3>
        <div className="about__skills">{skills}</div>
      </div>
    </section>
  </ScrollableAnchor>
)

export default About



