import React, { Component } from 'react'
import ScrollableAnchor from 'react-scrollable-anchor'
import ReCAPTCHA from 'react-google-recaptcha'
import * as emailjs from 'emailjs-com'

import Aux from '../../hoc/Aux/Aux'
import Icon from '../../UI/Icon/Icon'
import Spinner from '../../UI/Spinner/Spinner'

class Contact extends Component {
  state = {
    formDetails: {
      senderName: '',
      senderEmail: '',
      message: '',
    },
    formSubmitted: false,
    submissionSucces: null,
    submissionFailReason: null,
  }

  onChangeHandler(event, type) {
    let value = event.target.value
    let oldFormDetails = { ...this.state.formDetails }
    if (type === 'name') {
      oldFormDetails.senderName = value
      this.setState({
        formDetails: oldFormDetails,
      })
    } else if (type === 'email') {
      oldFormDetails.senderEmail = value
      this.setState({
        formDetails: oldFormDetails,
      })
    } else if (type === 'message') {
      oldFormDetails.message = value
      this.setState({
        formDetails: oldFormDetails,
      })
    }
  }

  onSubmitHandler(event) {
    event.preventDefault()
    let oldFormDetails = { ...this.state.formDetails }
    this.setState({
      formDetails: oldFormDetails,
      formSubmitted: true,
    })

    emailjs
      .send(
        'mailgun',
        'contact_form',
        {
          senderName: this.state.formDetails.senderName,
          senderEmail: this.state.formDetails.senderEmail,
          message: this.state.formDetails.message,
        },
        'user_V4dyIgSYTkwbJlRI6yzDb'
      )
      .then(
        response => {
          this.setState({
            formDetails: oldFormDetails,
            formSubmitted: true,
            submissionSucces: true,
          })
        },
        err => {
          let errReason = JSON.parse(err.text)
          if (errReason.error === 'CAPTCHA data missing') {
            errReason = 'CAPTCHA data missing'
          } else {
            errReason = null
          }
          this.setState({
            formDetails: oldFormDetails,
            formSubmitted: true,
            submissionSucces: false,
            submissionFailReason: errReason,
          })
        }
      )
  }

  handleFormReset(event) {
    event.preventDefault()
    let oldFormDetails = { ...this.state.formDetails }
    this.setState({
      formDetails: oldFormDetails,
      formSubmitted: false,
      submissionSucces: null,
      submissionFailReason: null,
    })
  }

  render() {
    let submission = null
    if (this.state.formSubmitted === false) {
      submission = (
        <Aux>
          <div className="form__group">
            <ReCAPTCHA sitekey="6LfGdHEUAAAAAP457yGG2QHRpVlLujDEVzNmU_y9" />
          </div>
          <div className="form__group">
            <input type="submit" value="Send it!" className="btn btn--full" />
          </div>
        </Aux>
      )
    } else if (
      this.state.formSubmitted === true &&
      this.state.submissionSucces === null
    ) {
      submission = (
        <div className="form__group">
          <Spinner />
        </div>
      )
    } else if (
      this.state.formSubmitted === true &&
      this.state.submissionSucces === true
    ) {
      submission = (
        <div className="form__group">
          <p className="form__icon">
            <Icon clName="form__icon-success" icName="check" />
            Message Sent!
          </p>
        </div>
      )
    } else if (
      this.state.formSubmitted === true &&
      this.state.submissionSucces === false
    ) {
      let failReason = ''
      if (this.state.submissionFailReason === 'CAPTCHA data missing') {
        failReason = 'Captcha Failed'
      } else {
        failReason = 'Something went wrong!'
      }
      submission = (
        <div className="form__group">
          <p className="form__icon">
            <Icon clName="form__icon-fail" icName="circle-with-cross" />
            {failReason}
            <button
              className="form__icon-resetbtn"
              onClick={event => this.handleFormReset(event)}
            >
              Try Again
            </button>
          </p>
        </div>
      )
    }
    return (
      <ScrollableAnchor id={'section-contact'}>
        <section className="section-contact">
          <h2 className="heading__secondary">
            Contact <span>Me</span>
          </h2>
          <div className="contact">
            <div className="contact__form">
              <h3 className="heading__tertiary">Send a message</h3>
              <form
                onSubmit={event => this.onSubmitHandler(event)}
                className="form"
              >
                <div className="form__group">
                  <input
                    type="text"
                    placeholder="Name"
                    className="form__input"
                    id="name"
                    onChange={event => this.onChangeHandler(event, 'name')}
                    value={this.state.senderName}
                    required
                    disabled={this.state.formSubmitted}
                  />
                  <label htmlFor="name" className="form__label">
                    Name
                  </label>
                </div>
                <div className="form__group">
                  <input
                    type="email"
                    placeholder="Email"
                    className="form__input"
                    id="email"
                    onChange={event => this.onChangeHandler(event, 'email')}
                    value={this.state.senderEmail}
                    required
                    disabled={this.state.formSubmitted}
                  />
                  <label htmlFor="email" className="form__label">
                    Email
                  </label>
                </div>
                <div className="form__group">
                  <textarea
                    name="message"
                    id="message"
                    onChange={event => this.onChangeHandler(event, 'message')}
                    className="form__textarea"
                    required
                    placeholder="Enter your message"
                    value={this.state.message}
                    disabled={this.state.formSubmitted}
                  />

                  <label htmlFor="message" className="form__label">
                    Message
                  </label>
                </div>
                {submission}
              </form>
            </div>
            <div className="contact__info">
              <h3 className="heading__tertiary">Contact Info</h3>
              <div className="contact__info-paragraph">
                <p className="paragraph">
                  <span className="paragraph__bold">Ray Bernard</span>
                  <br />Full Stack Web Developer, Designer, Entrepreneur
                </p>
                <div className="paragraph">
                  <div className="contact__info-details">
                    <Icon clName="contact__info-icon" icName="location-pin" />
                    Calgary, Alberta, Canada
                  </div>
                  <div className="contact__info-details">
                    <Icon clName="contact__info-icon" icName="phone" />
                    +1 (403) 744-5150
                  </div>
                  <div className="contact__info-details">
                    <Icon clName="contact__info-icon" icName="mail" />
                    <a
                      href="mailto:info@rbernard.ca"
                      className="paragraph__link"
                    >
                      info@rbernard.ca
                    </a>
                  </div>
                  <div className="contact__info-details">
                    <Icon clName="contact__info-icon" icName="download" />
                    <a
                      href="/static/Ray_Bernard_Resume.pdf"
                      target="_blank"
                      className="paragraph__link"
                    >
                      Download Resume
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </ScrollableAnchor>
    )
  }
}

export default Contact



