import React from 'react'

import Icon from '../../UI/Icon/Icon'
import logo from '../../../assets/img/logo-yellow-white.png'

const Header = () => (
  <header className="section-header">
    <div className="header">
      <div className="header__logo-box">
        <img src={logo} alt="RBERNARD Logo" className="header__logo" />
      </div>
      <div className="header__text-box">
        <h1 className="heading__primary">
          <div className="heading__primary-main">
            Ray <span>Bernard</span>
          </div>
          <div className="heading__primary-sub">
            Full Stack Web Developer, Designer, Entrepreneur
          </div>
        </h1>
        <div className="header__links">
          <div className="header__buttons">
            <a
              href="/static/Ray_Bernard_Resume.pdf"
              target="_blank"
              className="btn btn--full"
            >
              Resume PDF
            </a>
            <a href="#section-portfolio" className="btn btn--empty">
              Portfolio
            </a>
          </div>
          <div className="socialmedia__icons">
            <a
              href="https://ca.linkedin.com/in/ray-bernard-3274a335"
              target="_blank"
            >
              <Icon clName="socialmedia__icons-linkedin" icName="linkedin" />
            </a>
            <a href="https://github.com/RayBDev" target="_blank">
              <Icon clName="socialmedia__icons-github" icName="github" />
            </a>
          </div>
        </div>
      </div>
    </div>
  </header>
)

export default Header



